<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-3 col-md-4 left-content" v-if="userData">
        <div class="photo" :style="{'backgroundImage': `url(${userData.photoURL})`}"></div>
        <div class="name"><p>曾霈宭</p></div>
        <div class="bio">
          <p>音樂專長：吉他、電吉他</p>
          <p>曲風：搖滾、爵士</p>
        </div>
        <button class="btn btn-outline-light" data-toggle="modal" data-target="#detail-modal">詳細資訊</button>
        <div class="editors"
          :class="{'menuSticky': tempDom.menuSticky}">
          <a href="personal-postmusic.html" class="links">
            <i class="fas fa-plus-circle"></i> 發佈音樂
          </a>
          <a href="personal-managepost.html" class="links">
            <i class="fas fa-cog"></i> 管理貼文
          </a>
          <a href="personal-manageaccount.html" class="links">
            <i class="fas fa-user-cog"></i>編輯帳號
          </a>
        </div>
      </div>
      <div class="col-lg-9 col-md-8 right-content">
        <br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';

export default {
  data() {
    return {      
      userData: null,
      tempDom: {
        menuSticky: false, 
      }
    }
  },
  methods: {    
    getUserData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.$http.post(url, {"url": `users/${user.uid}/basic/`}).then((response) => {
            vm.userData = response.data;
          })
        } else {
          vm.userData = null;
        }
      });
    },
    scrollMethod(scrollValue) {
      const vm = this;
      
    }
  },
  created() {
    const vm = this;
    vm.getUserData();
  },
}
</script>

<style lang="scss" scoped>
.left-content {
  background-color: #fd8622;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-image: url(../assets/img/bg/wave-bg.svg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 270%;
  height: 100vh;
  .photo {
    background-position: center;
    background-size: cover;
    border-radius: 50%;
    width: 160px;
    height: 160px;
    border: 8px solid #ff9b34;
    box-shadow: 0 0 4px #fff;
    position: relative;
    overflow: hidden;
  }
  .name {
    height: 70px;
    width: 100%;
    background-image: url(../assets/img/bg/hr.svg);
    background-size: 100% 100%;
    margin-top: 5px;
    p {
    font-size: 20px;
    padding-top: 3px;
    padding-left: 16px;
    font-weight: bold;
    color: #fff;
    } 
  }
  .bio {
    margin: 15px 0;
    width: 85%;
    p {
    max-width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: #fff;
    display: block;
    font-size: 14px;
    font-weight: 400;
    margin: 0 0 2px;
    }
  }
  .btn{
    align-self: flex-end;
  }
  .editors{
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    align-self: flex-end;
    .links{
      color: rgba(50, 50, 50, 0.8);
      transition: all 0.2s ease-in-out;
      &:hover{
        text-decoration: none;
        color: #fff;
      }
    }
  }
  @media (max-width: 767px){
    height: auto;
    // padding: 30px 0 0 0;
    padding-top: 30px;
    padding-bottom: 70px;
    background-position: 55%;
    overflow: hidden;
    .photo{
      width: 120px;
      height: 120px;
      border: 5px solid #ff9b34;
    }
    .name {
      background-image: url(../assets/img/bg/hr-sm.svg);
      padding: 0 10%;
    }
    .bio{
      display: none;
    }
    .btn{
      margin-right: 8%;
    }
    .editors{
      flex-direction: row;
      margin-top: 15px;
      width: 100%;
      justify-content: space-between;
      background-color: rgba(255, 255, 255, 0.8);
      position: absolute;
      bottom: 0;
      left: 0;
      &.menuSticky{
        position: fixed;
        top: 50px;
      }
      .links{
        flex: 1;
        text-align: center;
        display: block;
        padding: 13px 0;
        position: relative;
        // border-left: 1px solid rgba(#000, .3);
        &::before{
          content: '';
          height: calc(100% - 25px);
          width: 1px;
          background-color: rgba(#000, .3);
          position: absolute;
          left: 0;
        }
        &:first-child{
          border: none;
          &::before{
            content: none;
          }
        }
        &:hover{
          background-color: rgba(50, 50, 50, 0.8)
        }
      }
    }
  }
}
</style>
